import {createContext, useContext, useEffect, useState} from "react";
import {Helper} from "../constants/Helper";

const getCategories = async () => {
    return await fetch(`${Helper.url}/api/category/index`)
        .then(response => response.json())
        .then(result => result.results)
        .catch(error => console.error(error))
}

export const DynamicTableContext = createContext({});

export const DynamicTableProvider = ({children}: {
    children: any
}) => {
    const [categories, _setCategories] = useState([])
    const [properties, setProperties] = useState([])
    const setCategories = (value:any) => {
        _setCategories(value)
        setProperties(value.properties)
    }
    useEffect(() => {
        getCategories()
            .then(result => setCategories(result))
            .catch(error => console.error(error))
    }, []);
    return (
        <DynamicTableContext.Provider value={{
            categories: categories,
            properties: properties,
        }}>
            {children}
        </DynamicTableContext.Provider>
    )
}

export const useDynamicTableContext = () => useContext(DynamicTableContext)