import {useEffect, useState} from "react";
import {Button, Col, Form, Modal} from "react-bootstrap";
import {Helper} from "../../constants/Helper";
import "./DynamicTable.css"

const defaultCurrent = {
    category: null,
    count: null,
    requirements: [
        {property: null, type: null, value: null, dimension: null}
    ]
};
const getCategories = async () => {
    return fetch(`${Helper.url}/api/category/index`, {
        method: "GET",
        credentials: "include",
        headers: new Headers({
            Authorization: `Bearer ${window.token}`,
        })
    })
        .then(response => response.json())
        .then(result => result.results)
        .catch(error => console.error(error))
}

const Table = ({data, update, remove}) => (
    <table className={"table table-striped"}>
        <tbody>
        {data.map((item, index) => (
            <tr key={`row-${index}`}>
                <td className={"col-4"}>{item.category?.title}<input name={`Order[filters][${index}][category_id]`}
                                                                     type={"hidden"}
                                                                     value={item.category?.id}/></td>
                <td className={"col-2"}>{item.count}<input name={`Order[filters][${index}][count]`} type={"hidden"}
                                                           value={item.count}/>
                </td>
                <td className={"col-5"}>
                    {item.requirements.map((requirement, requirementIndex) => (
                        <div className={"text-left"} key={`requirement-${index}-${requirementIndex}`}>
                            {requirement.property?.title} {requirement.type} {requirement.value} {requirement.dimension?.title}
                            <input name={`Order[filters][${index}][requirements][${requirementIndex}][property_id]`}
                                   type={"hidden"} value={requirement.property?.id}/>
                            <input name={`Order[filters][${index}][requirements][${requirementIndex}][type]`}
                                   type={"hidden"} value={requirement.type}/>
                            <input name={`Order[filters][${index}][requirements][${requirementIndex}][value]`}
                                   type={"hidden"} value={requirement.value}/>
                            <input
                                name={`Order[filters][${index}][requirements][${requirementIndex}][dimension_id]`}
                                type={"hidden"} value={requirement.dimension?.id}/>
                        </div>
                    ))}
                </td>
                <td className={"col-1"}>
                        <span className={"text-primary"}> <i className={"fas fa-pencil"}
                                                             onClick={() => update(index)}></i></span>
                    <span className={"text-primary"}> <i className={"fas fa-trash"}
                                                         onClick={() => remove(index)}></i></span>
                </td>
            </tr>
        ))}
        </tbody>
    </table>
)


export default function DynamicTable() {
    const [dynamicTableModal, setDynamicTableModal] = useState(false)
    const [categories, setCategories] = useState([])
    const [data, setData] = useState([])
    const [current, setCurrent] = useState({})
    const [update, setUpdate] = useState(null)

    const handleModalClick = () => {
        setUpdate(null)
        setCurrent(defaultCurrent)
        setDynamicTableModal(true)
    }
    const handleConfirmModal = () => {
        if (update !== null) {
            setData(data.map((item, index) => update === index ? current : item))
        } else {
            setData([...data, current])
        }
        setCurrent({})
        setDynamicTableModal(false)
    }
    const handleDismissModal = () => {
        setCurrent({})
        setDynamicTableModal(false)
    }
    const handleUpdate = (value) => {
        setUpdate(value)
        setCurrent(data[value])
        setDynamicTableModal(true)
    }
    const handleRemove = (value) => {
        setData(data.filter((item, index) => {
            return index !== value
        }))
    }
    const updateRequirement = (updateIndex, value) => {
        const r = current.requirements.map((item, index) => index === updateIndex ? {...item, ...value} : item)
        setCurrent({...current, ...{requirements: r}})
    }

    useEffect(() => {
        getCategories()
            .then(result => {
                setCategories(result)
            })
    }, []);

    return (
        <div id={"my-table"} className={"container-fluid"}>
            <Button variant={"primary"} onClick={handleModalClick} className={"mb-3"}>Добавить свойство</Button>
            <Table data={data} update={handleUpdate} remove={handleRemove}/>
            <Modal show={dynamicTableModal} onHide={handleDismissModal} size={"lg"}>
                <Modal.Header backdrop={"static"}>
                    <Modal.Title>Добавить свойство</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form>
                        {/* Current Category  */}
                        <Form.Group value={current.category?.title} className={"mb-3"}>
                            <Form.Control as={"select"} defaultValue={current.category?.id} onChange={(event) => {
                                const c = Helper.findById(categories, Number.parseInt(event.target.value));
                                if (c) {
                                    setCurrent({...current, ...{category: c}})
                                } else {
                                    setCurrent(defaultCurrent)
                                }
                            }}>
                                <option>Выберите категорию</option>
                                {categories && categories.map((category, index) => (
                                    <option key={`category-${index}`} value={category?.id}>{category?.title}</option>))}
                            </Form.Control>
                        </Form.Group>
                        {/* Current Count     */}
                        <Form.Group value={current?.count} className={"mb-3"}>
                            <Form.Control type={"number"} value={current?.count} disabled={!current?.category}
                                          onChange={(event) => {
                                              const value = Number.parseInt(event.target.value)
                                              if (value) {
                                                  setCurrent({...current, ...{count: value}})
                                              } else {
                                                  setCurrent({...current, ...{count: 0}})
                                              }
                                          }}/>
                        </Form.Group>
                        {/* Current Requirements */}
                        {current?.requirements?.map((requirement, requirementIndex) => (
                            <div className={"row"}>
                                {/* Requirement Property  */}
                                <Form.Group as={Col}>
                                    <Form.Control as={"select"} value={requirement?.property?.id} onChange={(event) => {
                                        updateRequirement(requirementIndex, {property: Helper.findById(current.category?.properties, Number.parseInt(event.target.value))})
                                    }} disabled={!(current?.category && current?.count)}>
                                        <option>Выберите свойство</option>
                                        {current?.category?.properties?.map((property, propertyIndex) => (
                                            <option value={property.id}
                                                    key={`requirement-${requirementIndex}-property-${property.id}`}>{property.title}</option>
                                        ))}
                                    </Form.Control>
                                </Form.Group>
                                {/* Requirement Type      */}
                                <Form.Group as={Col}>
                                    <Form.Control as={"select"} value={requirement?.type} onChange={(event) => {
                                        updateRequirement(requirementIndex, {type: event.target.value})
                                    }}>
                                        <option>Сравнение</option>
                                        {Helper.types.map((type, typeIndex) => (
                                            <option value={type}
                                                    key={`requirement-${requirementIndex}-type-${typeIndex}`}>{type}</option>
                                        ))}
                                    </Form.Control>
                                </Form.Group>
                                {/* Requirement Value     */}
                                <Form.Group as={Col}>
                                    <Form.Control type={"number"} value={requirement?.value} onChange={event => {
                                        updateRequirement(requirementIndex, {value: Number.parseInt(event.target.value)})
                                    }}/>
                                </Form.Group>
                                {/* Requirement Dimension */}
                                <Form.Group as={Col}>
                                    <Form.Control as={"select"} value={requirement?.dimension?.id} onChange={event => {
                                        updateRequirement(requirementIndex, {dimension: Helper.findById(requirement.property.dimensions, Number.parseInt(event.target.value))})
                                    }}>
                                        <option>Выберите единицу значения</option>
                                        {requirement?.property && requirement.property.dimensions?.map(dimension => (
                                            <option value={dimension.id}
                                                    key={`requirement-${requirementIndex}-dimension-${dimension.id}`}>{dimension.title}</option>
                                        ))}
                                    </Form.Control>
                                </Form.Group>
                            </div>
                        ))}
                    </Form>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant={"danger"} onClick={handleDismissModal}>Отмена</Button>
                    <Button variant={"primary"} onClick={handleConfirmModal}>Сохранить</Button>
                </Modal.Footer>
            </Modal>
        </div>
    )
}