import React from 'react';
import ReactDOM from 'react-dom/client';
import reportWebVitals from './reportWebVitals';
import {Calendar, CoworkerForm, CoworkerView, DynamicTable} from "./components";
import {CalendarProvider, DynamicTableProvider, LoaderProvider} from "./hooks";

const list = document.getElementsByClassName("calendar");
if (list) {
    // @ts-ignore
    for (const element of list) {
        const domElement = ReactDOM.createRoot(element);
        const attributes = Object.fromEntries(Array.from(element.attributes).map((item: any) => [item.name, item.value]))
        domElement.render(
            <CalendarProvider>
                <Calendar {...attributes} />
            </CalendarProvider>
        )
    }
}

const dTables = document.getElementsByClassName("dynamicTable");
if (dTables) {
    // @ts-ignore
    for (const table of dTables) {
        const domElement = ReactDOM.createRoot(table);
        const attributes = Object.fromEntries(Array.from(table.attributes).map((item: any) => [item.name, item.value]))
        console.log(attributes)
        domElement.render(
            <DynamicTableProvider>
                <DynamicTable {...attributes} />
            </DynamicTableProvider>
        )
    }
}

const profileContainer = document.getElementById('coworker-view')
if (profileContainer) {
    // @ts-ignore
    const domElement = ReactDOM.createRoot(profileContainer);
    domElement.render(
        <React.StrictMode>
            <CoworkerView {...profileContainer.dataset} />
        </React.StrictMode>
    )
}

const coworkerForm = document.getElementById('coworker-form')
if (coworkerForm) {
    const dom  =ReactDOM.createRoot(coworkerForm)
    dom.render(
        <React.StrictMode>
            <LoaderProvider>
                <CoworkerForm {...coworkerForm.dataset} />
            </LoaderProvider>
        </React.StrictMode>
    )
}

reportWebVitals();
