import React, {useEffect, useRef, useState} from "react";
import {Helper} from "../../constants/Helper";
import {Left, Right} from "../Icon";
import {useCalendarContext} from "../../hooks/CalendarContext";
import "./Calendar.css"
import {Button, Col, Form, Modal, Overlay, Tooltip} from "react-bootstrap";

const months = [
    "Январь",
    "Февраль",
    "Март",
    "Апрель",
    "Май",
    "Июнь",
    "Июль",
    "Август",
    "Сентябрь",
    "Октябрь",
    "Ноябрь",
    "Декабрь",
]
const getHours = async (year, month) => {
    return await fetch(`${Helper.url}/api/coworker/calendar-month?year=${year}&month=${month + 1}`, {
        headers: new Headers({
            Authorization: `Bearer ${window.token}`
        }),
        credentials: 'include'
    })
        .then(response => response.json())
        .catch(e => console.error(e))
}
const getOrderList = async (date, coworker_id) => {
    const newDate = date?.toISOString().split('T')[0]
    if (date && coworker_id) {
        return await fetch(`${Helper.url}/api/order/get-list?coworker_id=${coworker_id}&date=${newDate}`, {
            headers: new Headers({
                Authorization: `Bearer ${window.token}`
            }),
            credentials: 'include'
        })
            .then(response => response.json())
            .catch(error => console.error(error))
    }
}
const findByDate = (array, date) => {
    return array.find((element) => element.date === date);
}

const DayItem = ({day}) => (
    <td>{day}</td>
)
const Header = ({days}) => {
    const {month, year, prevMonth, nextMonth} = useCalendarContext()
    return (
        <thead>
        <tr key={"header"}>
            <td rowSpan={2} style={{verticalAlign: "middle", width: "150px"}}>
                <div className={"button"} onClick={_ => prevMonth()}>
                    <Left color={"#000"} size={24}/>
                    <span> Назад </span>
                </div>
            </td>
            <td colSpan={days}>
                <span>{months[month]} {year}</span>
            </td>
            <td rowSpan={2} style={{verticalAlign: "middle", width: "150px"}}>
                <div className={"button"} onClick={_ => nextMonth()}>
                    <span> Вперед </span>
                    <Right color={"#000"} size={24}/>
                </div>
            </td>
        </tr>
        <tr key={"days"}>
            {Array.from(Array(days).keys()).map((_, index) => (
                <DayItem day={index + 1} key={`${year}-${month}-${index}`}/>))}
        </tr>
        </thead>
    )
}
const TooltipContainer = ({content, tooltip, className = "", ...props}) => {
    const [visibility, setVisibility] = useState(false)

    const target = useRef(null);

    return (
        <div onMouseOver={() => setVisibility(true)} onMouseLeave={() => setVisibility(false)}
             className={['tooltip-container', className].join(" ")} {...props}>
            <div ref={target}>{content}</div>
            <Overlay target={target.current} show={visibility} placement="bottom">
                {(props) => (
                    <Tooltip id="overlay-example" {...props}>
                        {tooltip}
                    </Tooltip>
                )}
            </Overlay>
        </div>
    )
}
const TD = ({index, element, currentDate, onClickHandler}) => {
    const {month, year} = useCalendarContext()

    const newDate = new Date(year, month, index + 1)

    const [day, setDay] = useState({})
    const [setTooltipShow] = useState(false)
    const [dateStr, setDateStr] = useState("")

    useEffect(() => {
        setDay(findByDate(element.data, dateStr))
    }, [element, dateStr]);
    useEffect(() => {
        if (day) {
            day.sum = Number.parseInt(day?.count) * Number.parseInt(day?.current_price);
        }
    }, [day]);
    useEffect(() => {
        setDateStr(`${year}-${Helper.zeroPad(month + 1, 2)}-${Helper.zeroPad(index + 1)}`)
    }, [month, year, index]);

    if (day) {
        if (currentDate > newDate) {
            return (
                <td key={`${element.id}-${dateStr}`} data-key={dateStr} data-id={element.id}
                    data-price={day?.current_price} data-payed={day?.is_payed} onClick={onClickHandler}
                    className={day ? day?.is_payed ? "is-payed" : "un-payed" : "free"}
                    onMouseOver={() => setTooltipShow(true)}
                    onMouseLeave={() => setTooltipShow(false)}>
                    <TooltipContainer content={day ? day.count : 0} tooltip={day.sum}/>
                </td>
            )
        }
    } else {
        return (<td key={`${element.id}-${dateStr}`}
                    className={currentDate > newDate ? "free" : "disable"}>{currentDate > newDate ? 0 : ""}</td>)
    }
}
const BodyRow = ({element, days, onModal}) => {
    const currentDate = new Date();
    element.total = 0;
    return (
        <tr key={`coworker-${element.id}`}>
            <td>{element.name}</td>
            {Array.from(Array(days).keys()).map((_, index) => {
                return (
                    <TD key={`calendar-${index}`} index={index} element={element} onClickHandler={onModal}
                        currentDate={currentDate}/>)
            })}
            <td className={"disable final"}>
                <div style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "space-around",
                    width: "100%",
                }}>
                    <TooltipContainer content={element.payed_hours} tooltip={element.payed} className={"is-payed"}/>
                    <TooltipContainer content={element.un_payed_hours} tooltip={element.un_payed}
                                      className={"un-payed"}/>
                    <div className={"total"}>{element.payed_hours + element.un_payed_hours}</div>
                </div>
            </td>
        </tr>
    )
}
const Footer = ({data, days}) => {
    let payed_hours = 0, un_payed_hours = 0, payed_sum = 0, un_payed_sum = 0;
    if (data) {
        for (const element of data) {
            payed_hours += Number.parseInt(element.payed_hours);
            un_payed_hours += Number.parseInt(element.un_payed_hours);
            payed_sum += Number.parseInt(element.payed);
            un_payed_sum += Number.parseInt(element.un_payed);
        }
        return (
            <tfoot>
            <tr>
                <td colSpan={days + 1}>Итого<span style={{display: "inline-block", width: "10px"}}></span></td>
                <td className={"final"}>
                    <TooltipContainer content={payed_hours} tooltip={payed_sum} className={"is-payed"}/>
                    <TooltipContainer content={un_payed_hours} tooltip={un_payed_sum} className={"un-payed"}/>
                    <div className={"total"}>{payed_hours + un_payed_hours}</div>
                </td>
            </tr>
            </tfoot>
        )
    }
}
const Body = ({data, days, onModal}) => {
    if (Array.isArray(data)) {
        return (
            <tbody>
            {data && data.map((element, index) => (
                <BodyRow element={element} key={`coworker-${index}`} days={days} onModal={onModal}/>
            ))}
            </tbody>
        )
    }
}

function Table({onModal, trigger}) {
    const {month, year} = useCalendarContext()
    const [days, setDays] = useState((year && month) ? (new Date(year, month + 1, 0)).getDate() : 0)
    const [info, setInfo] = useState([])
    useEffect(() => {
        if (year && month) {
            setDays((new Date(year, month + 1, 0)).getDate())
        }
    }, [month, year]);
    useEffect(() => {
        getHours(year, month)
            .then(result => setInfo(result))
    }, [trigger, year, month]);

    return (
        <table className={"table table-striped table-hover"}>
            <Header days={days}/>
            <Body data={info} days={days} onModal={onModal}/>
            <Footer data={info} days={days}/>
        </table>
    )
}

const Calendar = () => {
    const [calendarModal, setCalendarModal] = useState(false)
    const [orderList, setOrderList] = useState([])
    const [orderId, setOrderId] = useState("")
    const [count, setCount] = useState(0)
    const [isPayed, setIsPayed] = useState(false)
    const [date, setDate] = useState()
    const [price, setPrice] = useState(0)
    const [coworkerId, setCoworkerId] = useState("")
    const [counter, setCounter] = useState(null)
    const {coworker: {setId}} = useCalendarContext()

    const onModal = (event) => {
        const date = new Date(event.currentTarget.getAttribute('data-key'));
        setDate(date)
        const elements = document.getElementsByClassName('modal');
        if (elements) {
            for (const element of elements) {
                element.style = "display: block";
            }
            const coworkerUID = event.currentTarget.getAttribute('data-id');
            setCoworkerId(coworkerUID);
            setId(coworkerUID);
            setCalendarModal(true);
        }
    }
    const onClose = (event) => {
        setDate(new Date())
        setCount(0)
        setIsPayed(false)
        setOrderId(null)
        setCalendarModal(false)
    }
    const onConfirm = () => {
        fetch(`${Helper.url}/api/order/set-hours`, {
            method: 'POST',
            headers: new Headers({
                Authorization: `Bearer ${window.token}`,
            }),
            credentials: 'include',
            body: Helper.buildForm({
                coworker_id: coworkerId,
                date: date?.toISOString().split('T')[0],
                order_id: orderId,
                count: count,
                is_payed: isPayed ? 1 : 0
            })
        })
            .then(response => {
                setCounter(count + 1);
            })
            .catch(error => console.error(error))
            .finally(() => {
                setCalendarModal(false)
            })
    }
    useEffect(() => {
        if (calendarModal) {
            fetch(`${Helper.url}/api/hours/get-hours?coworker_id=${coworkerId}&date=${date.toISOString().split('T')[0]}`)
                .then(response => response.json())
                .then(response => {
                    const model = response.model;
                    if (model) {
                        setDate(new Date(model.date))
                        setCount(model.count)
                        setIsPayed(model.is_payed)
                        setOrderId(model.order_id)
                        setCoworkerId(model.coworker_id)
                        setPrice(model.current_price)
                    }
                })
                .catch(error => console.error(error))
        }
    }, [calendarModal, coworkerId, date]);
    useEffect(() => {
        getOrderList(date, coworkerId)
            .then(result => {
                setOrderList(result)
            })
    }, [coworkerId, date]);
    return (
        <div id={"my-calendar"}>
            <Table onModal={onModal} trigger={counter}/>
            <Modal show={calendarModal} onHide={onClose} size={"lg"}>
                <Modal.Header backdrop={"static"}>
                    <Modal.Title>Почасовка</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form.Group value={date?.toISOString().split("T")[0]}>
                        <Form.Control type={"hidden"} onChange={event => setDate(event.target.value)}/>
                    </Form.Group>
                    <Form.Group className={"mb-3"}>
                        <Form.Label column={true}>Заказ</Form.Label>
                        <Form.Control as={'select'} value={orderId} disabled={!orderList?.length} onChange={event => {
                            setOrderId(Number.parseInt(event.target.value))
                        }}>
                            {orderList?.length === 0 ? <option>Ничего не найдено</option> : ""}
                            {orderList && orderList.map(element => {
                                console.log(element)
                                return <option value={element} key={`order-list-${element}`}>Заказ #{element}</option>
                            })}
                        </Form.Control>
                    </Form.Group>
                    <Form.Group className={"mb-3"}>
                        <Form.Label column={true}>Количество</Form.Label>
                        <Form.Control value={count} type={"number"} onChange={event => {
                            setCount(Number.parseInt(event.target.value))
                        }} disabled={!orderId}/>
                    </Form.Group>
                    <Form.Group className={"mb-3"}>
                        <Form.Label column={true}>Стоимость</Form.Label>
                        <Form.Control type={"text"} value={price} disabled={true}/>
                    </Form.Group>
                    <Form.Group className={"mb-3"}>
                        <Col sm={{span: 10, offset: 3}}></Col>
                        <Form.Check type={"checkbox"} checked={isPayed} label={"Оплачено"}
                                    disabled={!(orderId && count)} onChange={() => {
                            setIsPayed(!isPayed)
                        }}/>
                    </Form.Group>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant={"danger"} onClick={onClose}>
                        Отмена
                    </Button>
                    <Button variant={"success"} onClick={onConfirm}>
                        Сохранить
                    </Button>
                </Modal.Footer>
            </Modal>
        </div>
    )
}

export default Calendar