import {Card, ListGroup, Table} from "react-bootstrap";
import React, {useEffect, useState} from "react";
import {Helper} from "../../constants/Helper"
import "./CoworkerView.css"

const CoworkerView = ({...props}) => {
    const [userId] = useState(props.userKey)
    const [user, setUser] = useState({})
    const [activeKey, setActiveKey] = useState(0)

    useEffect(() => {
        fetch(`${Helper.url}/api/user/info?id=${userId}`, {
            headers: new Headers({
                Authorization: `Bearer ${window.token}`
            }),
            method: "GET",
        })
            .then(response => response.json())
            .then(data => setUser(data))
            .catch(error => console.error(error))
    }, [userId]);
    useEffect(() => {
        console.log(userId)
    }, [userId]);

    return (
        <div className={"container-fluid"}>
            {user &&
                <div className={"row"}>
                    <div className={"col-3"}>
                        <ListGroup as={"ul"} size={"xs"}>
                            <ListGroup.Item as={"li"} active={activeKey === 0} onClick={() => {
                                setActiveKey(0)
                            }}>Account</ListGroup.Item>
                            <ListGroup.Item as={"li"} active={activeKey === 1} onClick={() => {
                                setActiveKey(1)
                            }}>Profile</ListGroup.Item>
                        </ListGroup>
                    </div>
                    <div className={"col-9"}>
                        <Card style={{display: activeKey === 0 ? "block" : "none"}} size={"sm"}>
                            <Card.Body>
                                <p>Account</p>
                                <Table striped={true} hover={true} bordered={false} size={"sm"}>
                                    <tbody>
                                    <tr>
                                        <td>Username</td>
                                        <td>{user.username}</td>
                                    </tr>
                                    <tr>
                                        <td>E-mail</td>
                                        <td>{user.email}</td>
                                    </tr>
                                    <tr>
                                        <td>Status</td>
                                        <td>{user.status}</td>
                                    </tr>
                                    </tbody>
                                </Table>
                            </Card.Body>
                        </Card>
                        <Card style={{display: activeKey === 1 ? "block" : "none"}}>
                            <Card.Body>
                                <h5>Profile</h5>
                                <Table striped={true} hover={true} bordered={false} size={"sm"}>
                                    <tbody>
                                    <tr>
                                        <td>Family</td>
                                        <td>{user?.profile?.family}</td>
                                    </tr>
                                    <tr>
                                        <td>Name</td>
                                        <td>{user?.profile?.name}</td>
                                    </tr>
                                    <tr>
                                        <td>Surname</td>
                                        <td>{user?.profile?.surname}</td>
                                    </tr>
                                    <tr>
                                        <td>Birthday</td>
                                        <td>{(new Date(user?.profile?.birthday)).toLocaleDateString()}</td>
                                    </tr>
                                    <tr>
                                        <td>Phone</td>
                                        <td>{user?.profile?.phone}</td>
                                    </tr>
                                    <tr>
                                        <td>Social</td>
                                        <td>
                                            {user?.profile?.chat_id ? <i className={"fab fa-telegram mx-2"} /> : ""}
                                            {user?.profile?.device_id ? <i className={"fas fa-mobile mx-2"} /> : ""}
                                        </td>
                                    </tr>
                                    </tbody>
                                </Table>
                            </Card.Body>
                        </Card>
                    </div>
                </div>
            }
        </div>
    )
}

export default CoworkerView